<template>
  <div id="app">
    <transition name="silde-fade">
      <keep-alive include="FnbpcFkjc">
        <router-view/>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
import mock from "./mock.js";
/* 侧边树 */
import { modelAsideQuery, dslAsideQuery } from "./mock.js";

let MSG_OPTIONS = {
  message: "",
  type: ""
};

export default {
  name: "AppRoot",
  mounted() {},
  provide() {
    return {
      APP: this
    };
  },
  data() {
    return {
      /* 侧边树 */
      modelAsideQuery,
      modelAsideList: [],
      modelTableHeaderTopList: [],
      model: mock.model,
      modelTableHeaderSubList: [],
      rules: mock.rules /* 单独拎出来是因为有可能会动态更改校验规则 */,
      dslAsideQuery,
      mock,
      modelInit: {} /* 用于reset的初始化数据 add*/,
      modelAdd: mock.model,
      modelAddTableHeaderSubList: [],
      /* 新增页面侧边树，用于缓存 */
      modelAddAsideTree: false
    };
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "xm"
        }
      };
      treeOptions.data = this.modelAsideList;
      return treeOptions;
    }
  },
  methods: {
    goTo(command) {
      this.$router.push({
        name: command
      });
    },
    c(...args) {
      console.log.apply(console, args);
    },
    async save({ loading } = {}) {
      /* loading框 */
      loading = loading || this.$loading();
      try {
        let doc = this.modelAdd;
        let list = this.modelAddTableHeaderSubList;
        debugger;
        /* save */
        let response = await this.$post("/proxy/save", {
          coll: "fnbpc_hyjc" /* collection */,
          doc /* document */,
          list
        });
        if (response.isSuccess) {
          /* 刷新列表 */
          MSG_OPTIONS = {
            type: "success",
            message: "保存成功"
          };
          /* 保存成功，更新当前展示的 */
          this.model = this.modelAdd;
          this.modelTableHeaderSubList = this.modelAddTableHeaderSubList;
          this.getAsideList();
          this.goTo("home");
        } else {
          throw new Error(response.data);
        }
      } catch (error) {
        MSG_OPTIONS = {
          type: "error",
          message: error.message
        };
      } finally {
        loading.close();
        this.$message({ showClose: true, ...MSG_OPTIONS });
      }
    },
    async remove({ loading }) {
      let isConfirm = await this.$confirm(`确实要删除当前的表单?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      });
      if (!isConfirm) return;
      loading = loading || this.$loading();
      let doc = this.model;
      try {
        let response = await this.$post("/proxy/delete", {
          coll: "fnbpc_jbzl" /* collection */,
          doc /* document */
        });
        if (response.isSuccess) {
          this.$message({
            showClose: true,
            message: "删除成功",
            type: "success"
          });
          this.model = {};
          this.getAsideList();
        } else {
          console.log(response.data);
          throw new Error("error");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "error"
        });
      } finally {
        loading.close();
      }
    },
    async getAsideList() {
      const loading = this.$loading();
      let condition = this.modelAsideQuery;
      try {
        let { isSuccess, data } = await this.$post("/proxy/get", {
          coll: "fnbpc_jbzl" /* collection */,
          doc: {},
          condition
        });
        if (isSuccess) {
          this.modelAsideList = data;
        }
      } catch (error) {
        console.log(error);
      }
      loading.close();
    },
    handleClickAside({ data, node, vm }) {
      console.log("handleClickAside", data);
      this.modelTableHeaderTopList = (sample =>
        [...Array(10).keys()].map(n => ({ ...sample, jcdh: n })))({
        jcdh: "检查单号",
        xmlb: "检查项目",
        hysj: Date.now(),
        hyys: "化验医生"
      });
    },
    handleClickTableHeaderTopRow(data, node, vm) {
      console.log("handleClickTableHeaderTopRow", data);
      this.model = Object.assign(
        {
          cysj: "2018-12-26",
          sjys: Date.now(),
          hysj: "2018-12-26",
          hyys: Date.now(),
          jcdh: "11",
          hyxm: "",
          hyjg: ""
        },
        data
      );
      this.modelTableHeaderSubList = (sample =>
        [...Array(100).keys()].map(n => ({ ...sample, jcdh: n + 1 })))({
        text: "项目名称",
        ywsx: "业务缩写",
        zlx: "值类型",
        bzz: Date.now(),
        dw: "单位",
        jg: "结果",
        wh: "操作"
      });
    }
  },
  watch: {
    model(newvalue) {
      console.clear();
      console.log("model", newvalue);
    }
  }
};
</script>

<style src="@c/styles/app.scss" lang="scss">
</style>
<style src="@c/styles/hqbj.scss" lang="scss">
</style>


