<template>
  <div class="view aside">
    <c-aside @clickAside="APP.handleClickAside"></c-aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="APP.goTo('add')">新增</el-button>
        <el-button
          @click="APP.goTo('update')"
          class="button"
          :disabled="!Boolean(APP.model.jcdh)"
        >修改</el-button>
        <el-button @click="APP.remove" :disabled="!Boolean(APP.model.jcdh)">删除</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <!-- top列表 -->
        <el-collapse-item :name="1">
          <el-table
            :data="APP.modelTableHeaderTopList"
            style="width: 99%"
            height="200"
            highlight-current-row
            @row-click="APP.handleClickTableHeaderTopRow"
          >
            <template v-for="(tableH, index) in APP.mock.dslTableHeaderTop">
              <el-table-column :prop="tableH.prop" :label="tableH.label" :key="index"></el-table-column>
            </template>
          </el-table>
        </el-collapse-item>
        <!-- 表单 -->
        <el-collapse-item :name="2">
          <!-- {{APP.c("dslArray:\n",APP.mock.dslArray)}} -->
          <c-table
            class="box-card"
            v-model="APP.model"
            :dsl="APP.mock.dslArray"
            :rules="APP.rules"
            :tableInfo="{cellspacing:2}"
            ref="topTabe"
            :disabled="true"
          />
        </el-collapse-item>
        <!-- 详情列表 -->
        <el-collapse-item :name="3">
          <el-table :data="APP.modelTableHeaderSubList" style="width: 99%" height="400">
            {{APP.c("dslTableHeaderSub:\n",APP.mock.dslTableHeaderSub)}}
            <template v-for="(tableH, index) in APP.mock.dslTableHeaderSub">
              <el-table-column
                :prop="tableH.prop"
                :label="tableH.label"
                :width="tableH.width"
                :key="index"
              ></el-table-column>
            </template>
          </el-table>
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";
import $ from "jquery";

export default {
  name: "FnbpcFkjc",
  mixins: [Main],
  inject: ["APP"],
  mounted() {
    this.APP.getAsideList();
  },
  data() {
    return {
      /* 动态数据 */
      asideTree: [],
      activeNames: [1, 2, 3]
    };
  },
  methods: {
    /* 完成刷新前的数据检测 */
    async save() {
      try {
        /* 验证表单是否都符合rule */
        if (await this.$refs.topTabe.valid()) {
          this.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
      }
    },
    getTitle(fix) {
      return this.APP.model.xm
        ? `${fix}-${this.APP.model.xm}-${this.APP.model.bh}`
        : fix;
    }
  }
};
</script>

