<template>
  <div class="view aside">
    <aside class="pickup">
      <c-pickup-tree
        :treeOptions="{props:
          {
          children:'children',
          label:'text'
          },
          data: APP.modelAddAsideTree,
          showCheckbox: true
      }"
        @check-change="handleCheckChange"
        ref="pickupTree"
      ></c-pickup-tree>
    </aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('home')">放弃编辑</el-button>
        <el-button @click="save" type="primary" class="button">保存</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <!-- 表单 -->
        <el-collapse-item :name="2">
          <!-- {{APP.c("dslArray:\n",APP.mock.dslArray)}} -->
          <c-table
            class="box-card"
            v-model="model"
            :dsl="APP.mock.dslArray"
            :rules="APP.rules"
            :tableInfo="{cellspacing:2}"
            ref="topTabe"
          />
        </el-collapse-item>
        <!-- 详情列表 -->
        <el-collapse-item :name="3">
          <el-table :data="modelList" style="width: 100%" height="400">
            <template v-for="(tableH, index) in APP.mock.dslTableHeaderSub">
              <el-table-column
                :prop="tableH.prop"
                :label="tableH.label"
                :width="tableH.width"
                :key="index"
              ></el-table-column>
            </template>
          </el-table>
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";

export default {
  name: "HqbjHjHyjc",
  mixins: [Main],
  inject: ["APP"],
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.warn("beforeRouteEnter", to.name);
      let command = to.name;
      const initCurrentModel = {
        add() {
          /* 置空 */
          vm.model = {
            cysj: "2018-12-26",
            sjys: "51340100101001",
            hysj: "2018-12-26",
            hyys: "51340100101001",
            jcdh: "",
            hyxm: "",
            hyjg: ""
          };
        },
        update() {
          /* 初始化新增model */
          /*           vm.$set(vm, "model", JSON.parse(JSON.stringify(vm.APP.model)));
          vm.$set( vm, "modelList", JSON.parse(JSON.stringify(vm.APP.modelTableHeaderSubList)) );
 */

          vm.model = JSON.parse(JSON.stringify(vm.APP.model));
          vm.modelList = JSON.parse(
            JSON.stringify(vm.APP.modelTableHeaderSubList)
          );
          debugger;
        }
      };
      initCurrentModel[command] && initCurrentModel[command]();
      /* 如果已经加载就用缓存的 */
      if (!vm.APP.modelAddAsideTree) {
        (async () => {
          let res = await vm.$post("/proxy/get", {
            coll: "dictionary" /* collection */,
            doc: { name: "HyjcAppendTree" }
          });
          if (res.data && res.data.length > 0 && res.data[0].data) {
            vm.$set(vm.APP, "modelAddAsideTree", res.data[0].data);
          }
        })();
      }
    });
  },
  mounted() {
    let aTree = this.$refs.pickupTree.getTree();
    aTree.setCheckedNodes([...this.modelList, ...[{ id: "0000000003" }]]);
  },
  data() {
    return {
      model: {},
      modelList: [],
      /* 动态数据 */
      activeNames: [2, 3]
    };
  },
  methods: {
    async goTo(command) {
      let isConfirm = await this.$confirm(`确实要放弃编辑?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      });
      if (!isConfirm) return;
      this.APP.goTo("home");
    },
    handleCheckChange() {
      let aTree = this.$refs.pickupTree.getTree();
      let isLeaf = true;
      let currentChecked = aTree.getCheckedNodes(isLeaf);
      console.log("currentChecked", currentChecked);
      let modelListStamp = [...this.modelList];

      currentChecked.map((checked, indexChecked) => {
        /* 如果已存在则跳过，不存在则增加 */
        for (let index = 0; index < modelListStamp.length; index++) {
          const element = modelListStamp[index];
          if (element.id === checked.id) {
            currentChecked[indexChecked] = element;
            return;
          }
        }
      });
      this.modelList = currentChecked;
    },
    deleteTableRow(row, index) {
      console.log("deleteTableRow", row, index);
      let stampModelTable = [...this.currentTableModelAdd];
      let target;
      const indexDelete = stampModelTable.findIndex(d => d.id === row.id);
      target = stampModelTable.splice(indexDelete, 1);
      /* 发送target到后台处理删除请求 */
      console.log("delete", target);
      /* 发送target到后台处理删除请求 */
      this.setCurrentTableModelAdd(stampModelTable);
    },
    async save() {
      try {
        /* 验证表单是否都符合rule */
        if (await this.$refs.topTabe.valid()) {
          this.APP.modelAdd = this.model;
          this.APP.modelAddTableHeaderSubList = this.modelList;
          this.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
      }
    }
  },
  computed: {}
};
</script>

